<template>
  <div class="calendar-wrapper">
    <btg-calendar :options="calendarOptions"
                  :refresh-func="fetchTickets"
                  v-on:clickDate="clickEvent"
                  v-on:clickEvent="clickEvent"></btg-calendar>
  </div>
</template>

<script>
import BtgCalendar from '../src/btg-calendar';
import mockData from './mockData'

export default {
  name: 'app',
  components: {BtgCalendar},
  data() {
    return {
      calendarOptions: {
        ticketsData: {},
        ticketCode: 'USBUEP1DUTEST',
        priceColor: [
          {
            value: 99999999,
            backgroundColor: '#ECF5FF',
            borderColor: '#B3D8FF',
            textColor: '#409EFF',
          }
        ],
        availableColor: [
          {
            value: 100,
            backgroundColor: '#FEF0F0',
            borderColor: '#FBC4C4',
            textColor: '#FF6F5B',
          },
          {
            value: 1000,
            backgroundColor: '#FEF0F0',
            borderColor: '#FBC4C4',
            textColor: '#FF6F5B',
          },
          {
            value: 99999999,
            backgroundColor: '#ECF8F2',
            borderColor: '#97D2B4',
            textColor: '#42B983',
          },
        ]
      }
    }
  },
  created() {
  },
  methods: {
    fetchTickets() {
      console.log('fetchTickets')
      setTimeout(()=>{
        const mock = mockData.data
        this.calendarOptions.ticketsData = mock
      }, 500)
    },
    clickEvent(event) {
      console.log(event)
    },
    clickDate(date) {
      console.log(date)
    }
  }
}
</script>

<style lang="scss">

.calendar-wrapper {
  width: 1000px;
  height: 800px;
}

</style>



